import React from "react";
import { debounce } from "next/dist/server/utils";
import { useCartStore } from "@/store/cart";

type ReturnProps = {
  totalAmount: number;
  items: any[];
  loading: boolean;
  addCartItem: (item: any) => void;
  updateItemQuantity: (id: number, quantity: number) => void;
  removeCartItem: (id: number) => void;
};

export const useCart = (runFetch?: boolean): ReturnProps => {
  // Get store values and functions. Assume the store functions are stable.
  const [
    totalAmount,
    items,
    fetchCartItems,
    loading,
    addCartItem,
    rawUpdateItemQuantity,
    removeCartItem,
  ] = useCartStore((state) => [
    state.totalAmount,
    state.items,
    state.fetchCartItems,
    state.loading,
    state.addCartItem,
    state.updateItemQuantity, // get the raw function
    state.removeCartItem,
  ]);

  // Memoize the debounced version so it remains stable.
  const updateItemQuantity = React.useMemo(
      () => debounce(rawUpdateItemQuantity, 200),
      [rawUpdateItemQuantity]
  );

  // Use an effect that runs only once on mount.
  React.useEffect(() => {
    if (runFetch) {
      fetchCartItems();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return {
    totalAmount,
    items,
    loading,
    addCartItem,
    updateItemQuantity,
    removeCartItem,
  };
};